.recipe-edit{
    position: fixed;
    padding: 30px;
    padding-top: 10px;
    right: 0;
    top: 0;
    width: 50%;
    max-height: 100%;
    overflow-y: auto;
}

.recipe-edit_remove-button-container{

    text-align: end;
}

.recipe-edit__remove-button{

    padding: 0;
    margin: 0;
    background: none;
    outline: none;
    font-size: 1.5rem;
}

.recipe-edit__details-grid{

    display: grid;
    grid-template-columns: auto 1fr;
    row-gap: 10px;
    column-gap: 40px;
}

.recipe-edit__label{

    font-weight: bold;

}

.recipe-edit__input{

border: 1px solid black;    
border-radius: 5px;
font-size: inherit;
padding: 5px 10px;
outline: none;
}

textarea.recipe-edit__input{
    height: 150px;
    resize: none;
}

.recipe-edit__ingredient-grid{

    display: grid;
    gap: 10px;
    grid-template-columns: repeat(3,auto);
    margin-top: 20px;
}

.recipe-edit-addingredient-btn-container{

    margin-top: 10px;
    text-align: center;
}

@media(max-width: 1000px){

    .recipe-edit__details-grid,
    .recipe-edit__ingredient-grid{

        grid-template-columns: 1fr;
    }

}